<template>
  <div class="card-creator" style="margin-top: 5vh; margin-bottom: 5vh;">
    <h2>Создание Поздравления</h2>
    <div v-if="selectedCard">
      <img :src="selectedCard.image" :alt="selectedCard.title" class="selected-card-image" />
      <p>Шаблон: {{ selectedCard.title }}</p>
    </div>
    <div class="customization">
      <label for="message">Ваше сообщение:</label>
      <textarea id="message" v-model="customMessage" placeholder="Введите ваше поздравление" style="width: 97%;"></textarea>
    </div>
    <button @click="createCard" class="create-button">Создать открытку</button>
  </div>
</template>

<script>
export default {
  name: 'CardCreator',
  data() {
    return {
      customMessage: '',
      cards: [
        { id: 1, image: 'https://avatars.mds.yandex.net/i?id=f646fb63cf882ec82fcca91a63efdd6d_sr-12487154-images-thumbs&n=13', title: 'Классическая'},
        { id: 2, image: 'https://static.mk.ru/upload/entities/2023/03/07/06/articlesImages/image/81/0a/84/fd/c890c5081c97d938036142c12ef13617.jpg', title: 'Весёлая'},
        { id: 3, image: 'https://i01.fotocdn.net/v1/140/anim_m/341/2708952203.jpg', title: 'Сердечная'},
        { id: 4, image: 'https://sun9-1.userapi.com/impg/9GSpsL2IqE6G7WgrgI-24LRQg0Yk3QF64ZJ2ng/Z322eqHDk_c.jpg?size=640x640&quality=95&sign=5bfa64fbc9fc59defabcd22178df8af6&c_uniq_tag=p_GiWNwq5dgxhOi6LP_Vq8q_0O1jhkC41E-ijQpd-eQ&type=album', title: 'Минималистская' },
        { id: 5, image: 'https://sun9-59.userapi.com/impg/32d0qcrbab7ZMiCuYqf9IVm6utnWpc-ircfJmg/Y6VRZd8-UsQ.jpg?size=604x604&quality=95&sign=9f85df9eae0d38c44389eef35adbc621&c_uniq_tag=Qh77-Qsi8m6MrZSIDGZ-2GuqGkHstFCs4YA41Ne_SqI&type=album', title: 'Цветочная'},
        { id: 6, image: 'https://sun9-9.userapi.com/impg/JI3MqdMOi2Vv__aTKGUz5AoP8aRb6wooXKTexw/qhLEjCJGaAA.jpg?size=828x828&quality=95&sign=ddf9a6398522a51daa3aa57c2a676971&c_uniq_tag=eICjS7dE2Ek5puJyfxaHEbclUyR1XvIqgX1xf_yZVjY&type=album', title: 'Яркая'},
        { id: 7, image: 'https://i.pinimg.com/736x/14/6b/b6/146bb6c6439626defff668902039eef7.jpg', title: 'Розовая'},
        { id: 8, image: 'https://i-mordovia.ru/upload/iblock/096/w9l4z7mfve18f3q6bpvclzate059onlq/2RNxhOjNKgw.jpg', title: 'Элегантная'},
      ],
    };
  },
  computed: {
    selectedCard() {
      const cardId = this.$route.params.cardId;
      return this.cards.find(card => card.id === parseInt(cardId));
    },
  },
  methods: {
    createCard() {
      alert(`Открытка создана`);
    },
  },
};
</script>

<style scoped>
.card-creator {
  max-width: 600px;
  margin: 0 auto;
  padding: 20px 20px;
  border: 1px solid #ddd;
  border-radius: 10px;
  background-color: #fff;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.selected-card-image {
  width: 100%;
  height: auto;
  margin-bottom: 20px;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}

.customization {
  margin-bottom: 20px;
}

textarea {
  width: 100%;
  height: 100px;
  padding: 10px;
  border-radius: 5px;
  border: 1px solid #ddd;
  resize: none;
  font-family: 'Arial', sans-serif;
}

.create-button {
  background-color: #f08080;
  color: white;
  padding: 12px 25px;
  border: none;
  border-radius: 25px;
  cursor: pointer;
  width: 100%;
  transition: background-color 0.3s ease;
  margin-top: 20px;
}

.create-button:hover {
  background-color: #ff9999;
}
</style>
