<template>
  <div class="gallery">
    <h2 style="text-align: center; margin-bottom: 5vh;">Галерея Открыток</h2>
    <div class="categories">
      <button v-for="category in categories" :key="category" class="category-button" @click="filterCategory(category)">{{ category }}</button>
    </div>
    <div class="cards">
      <div v-for="card in filteredCards" :key="card.id" @click="selectCard(card)" class="card">
        <img :src="card.image" :alt="card.title" class="card-image"/>
        <p @click="selectCard(card)" class="card-title">{{ card.title }}</p>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Gallery',
  data() {
    return {
      categories: ['Все', 'Классические', 'Весёлые', 'Сердечные', 'Минималистские'],
      selectedCategory: 'Все',
      cards: [
        { id: 1, image: 'https://avatars.mds.yandex.net/i?id=f646fb63cf882ec82fcca91a63efdd6d_sr-12487154-images-thumbs&n=13', title: 'Классическая', category: 'Классические' },
        { id: 2, image: 'https://static.mk.ru/upload/entities/2023/03/07/06/articlesImages/image/81/0a/84/fd/c890c5081c97d938036142c12ef13617.jpg', title: 'Весёлая', category: 'Весёлые' },
        { id: 3, image: 'https://i01.fotocdn.net/v1/140/anim_m/341/2708952203.jpg', title: 'Сердечная', category: 'Сердечные' },
        { id: 4, image: 'https://sun9-1.userapi.com/impg/9GSpsL2IqE6G7WgrgI-24LRQg0Yk3QF64ZJ2ng/Z322eqHDk_c.jpg?size=640x640&quality=95&sign=5bfa64fbc9fc59defabcd22178df8af6&c_uniq_tag=p_GiWNwq5dgxhOi6LP_Vq8q_0O1jhkC41E-ijQpd-eQ&type=album', title: 'Минималистская', category: 'Минималистские' },
        { id: 5, image: 'https://sun9-59.userapi.com/impg/32d0qcrbab7ZMiCuYqf9IVm6utnWpc-ircfJmg/Y6VRZd8-UsQ.jpg?size=604x604&quality=95&sign=9f85df9eae0d38c44389eef35adbc621&c_uniq_tag=Qh77-Qsi8m6MrZSIDGZ-2GuqGkHstFCs4YA41Ne_SqI&type=album', title: 'Цветочная', category: 'Классические' },
        { id: 6, image: 'https://sun9-9.userapi.com/impg/JI3MqdMOi2Vv__aTKGUz5AoP8aRb6wooXKTexw/qhLEjCJGaAA.jpg?size=828x828&quality=95&sign=ddf9a6398522a51daa3aa57c2a676971&c_uniq_tag=eICjS7dE2Ek5puJyfxaHEbclUyR1XvIqgX1xf_yZVjY&type=album', title: 'Яркая', category: 'Весёлые' },
        { id: 7, image: 'https://i.pinimg.com/736x/14/6b/b6/146bb6c6439626defff668902039eef7.jpg', title: 'Розовая', category: 'Сердечные' },
        { id: 8, image: 'https://i-mordovia.ru/upload/iblock/096/w9l4z7mfve18f3q6bpvclzate059onlq/2RNxhOjNKgw.jpg', title: 'Элегантная', category: 'Минималистские' },
      ],
    };
  },
  computed: {
    filteredCards() {
      if (this.selectedCategory === 'Все') {
        return this.cards;
      }
      return this.cards.filter(card => card.category === this.selectedCategory);
    },
  },
  methods: {
    filterCategory(category) {
      this.selectedCategory = category;
    },
    selectCard(card) {
      this.$router.push({ name: 'CardCreator', params: { cardId: card.id } });
    },
  },
};
</script>

<style scoped>
.gallery {
  padding: 40px 20px;
  background-color: #ffe4e1;
  color: #d2698a;
}

.categories {
  display: flex;
  gap: 25px;
  margin-bottom: 5vh;
  justify-content: center;
}

.category-button {
  background-color: #f08080;
  color: white;
  border: none;
  padding: 10px 20px;
  border-radius: 20px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.category-button:hover {
  background-color: #ff9999;
}

.cards {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 30px;
  width: 55%;
  margin: auto;
}

.card {
  width: 200px;
  text-align: center;
  margin-bottom: 20px;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  cursor: pointer;
}

.card-image {
  width: 100%;
  height: 200px;
  height: auto;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}

.card-title {
  margin-top: 15px;
  font-weight: bold;
  color: #d2698a;
  cursor: pointer;
  transition: color 0.3s ease;
}

.card-title:hover {
  color: #ff9999;
}

.card:hover {
  transform: scale(1.05);
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.3);
}
</style>
